.menu-container {
  position: relative;
  border-radius: 22px;
  width: 145px;
  float: right;
  margin-top: 5px;
  text-align: center;
}

.notification-container {
  text-align: center;
  position: relative;
  border-radius: 22px;
  float: left;
  margin-top: 5px;
  margin-right: 12px;
  width: 60px;
  cursor: pointer;
  z-index: 100;
}

.menu-container.open > .menu-button {
  color: #000;
}

.menu-container.open > .menu-dropdown {
  z-index: 100;
  opacity: 1;
  display: block;
  border-radius: 2px 2px 20px 20px;
  transition: opacity 0.2s, box-shadow 0.3s;
  transform: none;
}

.notification-container:hover {
  background-color: #6969ae;
  color: white;
}

.notification-container.open > .menu-dropdown {
  z-index: 1;
  opacity: 1;
  display: block;
  border-radius: 2px 2px 20px 20px;
  transition: opacity 0.2s, box-shadow 0.3s;
  transform: none;
}

.menu-button {
  background: none;
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent;
  background-color: #fff;
  border: none;
  padding: 0 8px;
  /* padding-right: 20px; */
  height: 44px;
  font-size: 16px;
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;
  outline: none !important;
  background-color: transparent;
  color: #fff;
  z-index: 1;
  position: relative;
}

.menu-button > img {
  height: 25px;
  margin-right: 13px;
}

.menu-dropdown {
  opacity: 0;
  display: none;
  background-color: #fff;
  border: 0px;
  padding: 0;
  position: absolute;
  left: 0px;
  top: 0px;
  right: 0px;
  overflow: hidden;
  border-radius: 22px 22px 11px 11px;
  transition: opacity 0.2s, box-shadow 0.4s, border-radius 0.4s,
    transform 0s 0.2s ease-out;
  transform: translateY(10px);
  transform-origin: 0 0;
  margin-top: 44px;
}

.menu-dropdown .content {
  overflow: hidden;
}

.menu-dropdown .content > ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.menu-dropdown .content > ul > li {
  list-style: none;
  font-size: 16px;
  padding: 8px;
  cursor: pointer;
  color: black;
}

.menu-dropdown > .menu-button {
  border: none;
  color: #000;
}

/* SiderBar for mobile header */
/* The sidebar menu */
.sidebar {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 999999;
  top: 0;
  right: 0;
  background-color: #0f1015;
  overflow-x: hidden;
  /* padding-top: 60px; */
  transition: 0.5s;
}

.sidebar a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
  transition: 0.3s;
}

.sidebar a:hover {
  color: #f1f1f1;
}

.sidebar .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 33px;
  margin-left: 50px;
}

.openbtn {
  font-size: 20px;
  cursor: pointer;
  background-color: #111;
  color: white;
  padding: 10px 15px;
  border: none;
}

.openbtn:hover {
  background-color: #444;
}

