.BGcontainer {
  background-color: #0f1015;
  align-items: center;
  justify-content: center;
  /* min-height: 45vh !important; */
  display: grid;
  margin-top: 1%;
}

.logintext {
  text-align: center;
  font-weight: 700;
  font-size: 16px;
  padding-bottom: 20px;
  color: white;
}

.verifyText {
  text-align: center;
  font-weight: 600;
  font-size: 14px;
  margin-top: 15px;
  color: #d3d3d3;
}

.form-label {
  font-size: 14px;
}

.inputfields {
  /* margin-top: 30px; */
  width: 100%;
  align-items: center;
  /* justify-content: center; */
  display: inline-grid;
  /* margin-left: 15%; */
  font-size: 12px;
}

.form-control {
  font-size: 12px;
}

.registerhere {
  text-align: center;
  margin-top: 10px;
  cursor: pointer;
}

.loginhere {
  text-align: center;
  margin-top: 30px;
  cursor: pointer;
}

.enableauthheader {
  text-align: center;
  font-weight: 700;
  margin-bottom: 30px;
  color: white;
}

.twoFACard {
  background-color: white !important;
  width: 28%;
  border-radius: 15px;
}

.autsteps {
  color: white;
  font-size: 14px !important;
}

.autsteps2 {
  color: white;
  margin-top: 10px !important;
  margin-bottom: 10px !important;
  font-size: 14px !important;
}

.qrcode {
  align-items: center;
  justify-content: center;
  display: flex;

  padding: 10px;
}

.qrcodetext {
  padding: 10px;
  display: flex;
  justify-content: center;
}

.enableauthbtns {
  display: flex;
  justify-content: space-evenly;
  margin-top: 10px;
  margin-bottom: 10px;
}

.login2FA {
  justify-content: center;
  display: flex;
  margin-top: 25px;
}

.qrtextvalue {
  font-size: 12px;
  text-align: center;
  font-weight: 700;
  width: 220px;
}

.topicon {
  height: 100px;
  justify-content: center;
  align-items: center;
  display: flex;
  margin-bottom: 15px;
}

.enterauth {
  width: 85%;
  display: block;
  justify-content: center;
  align-items: center;
}

.authfa {
  width: 90%;
  display: block;
  justify-content: center;
  align-items: center;
}

.registeredsuccesstext {
  font-size: 20px;
  text-align: center;
  color: #d3d3d3;
  font-weight: 500;
}

.tablecard {
  border-radius: 0px !important;
}

.iconemail {
  background: url("../images/login/email.png") no-repeat left;
  background-size: 20px;
  padding-left: 25px;
}

.iconpw {
  background: url("../images/login/password.png") no-repeat left;
  background-size: 20px;
  padding-left: 25px;
}

.iconuser {
  background: url("../images/login/user.png") no-repeat left;
  background-size: 20px;
  padding-left: 25px;
}

.iconchain {
  background: url("../images/login/chain.png") no-repeat left;
  background-size: 20px;
  padding-left: 25px;
}

.registerText {
  text-align: center;
  font-weight: 700;
  font-size: 16px;

  color: white;
}

.errorbox {
  border-radius: 10px;
  color: #f27878 !important;
  text-align: center;
  font-size: 14px;
  border: 1px solid #940707;
  padding: 5px;
  background-color: #3f1f1f;
}

.successrbox {
  color: #3fea38bf !important;
  text-align: center;
  font-size: 14px;
  border: 1px solid #105808;
  padding: 5px;
  background-color: #07310d;
}

